<template>
  <v-app>
    <router-view @showDialog="showDialog" @showSnackbar="showSnackbar" class="contenedor-pattern"></router-view>

    <v-dialog v-model="dialog" max-width="290">
      <v-card>
        <div class="mensaje">{{msg_dialog}}</div>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" text @click="dialog = false">Cerrar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-snackbar v-model="snackbar">
      {{ msg_snackbar }}
      <v-btn color="success" text @click="snackbar = false">Cerrar</v-btn>
    </v-snackbar>
  </v-app>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      msg_dialog: "",
      msg_snackbar: "",
      dialog: false,
      snackbar: false,
      // Timer
      timer: null,
      stopCountingWhenWindowIsInactive: true,
      updateTime: 10000
    };
  },
  mounted() {
    Array.prototype.sample = function() {
      return this[Math.floor(Math.random() * this.length)];
    };

    // Timer
    if (this.stopCountingWhenWindowIsInactive) {
      if (typeof document.hidden !== "undefined") {
        var hidden = "hidden",
          visibilityChange = "visibilitychange",
          visibilityState = "visibilityState";
      } else if (typeof document.msHidden !== "undefined") {
        var hidden = "msHidden",
          visibilityChange = "msvisibilitychange",
          visibilityState = "msVisibilityState";
      }
      var documentIsHidden = document[hidden];

      document.addEventListener(visibilityChange, () => {
        if (documentIsHidden != document[hidden]) {
          if (document[hidden]) {
            clearInterval(this.timer);
          } else {
            this.startCounting();
          }
          documentIsHidden = document[hidden];
        }
      });
    }
    this.startCounting();
  },
  methods: {
    showDialog(msg_dialog) {
      this.msg_dialog = msg_dialog;
      this.dialog = true;
    },
    showSnackbar(msg_snackbar) {
      this.msg_snackbar = msg_snackbar;
      this.snackbar = true;
    },
    obtenerFechaActual() {
      var today = new Date();
      var dd = today.getDate();
      var mm = today.getMonth() + 1; //January is 0!

      var yyyy = today.getFullYear();
      if (dd < 10) {
        dd = "0" + dd;
      }
      if (mm < 10) {
        mm = "0" + mm;
      }
      return dd + "/" + mm + "/" + yyyy;
    },
    //Timer
    getTimeSpentOnSite() {
      let timeSpentOnSite = parseInt(localStorage.getItem("timeSpentOnSite"));
      timeSpentOnSite = isNaN(timeSpentOnSite) ? 0 : timeSpentOnSite;
      return timeSpentOnSite;
    },
    startCounting() {
      let timerStart = Date.now();

      this.timer = setInterval(() => {
        let sesion = this.$store.state.servicio.sesion.obtenerSesion();
        let alumno_id = sesion["id"];
        let fecha = this.obtenerFechaActual();

        if (sesion["id"] && sesion["tipo"] == "alumno") {
          this.$store.state.servicio.tiempo.obtenerTiempoAlumno(
            alumno_id,
            fecha,
            res => {
              let timeSpentOnSite = res["tiempo"] + (Date.now() - timerStart);
              timerStart = parseInt(Date.now());

              this.$store.state.servicio.tiempo.actualizarTiempoAlumno(
                alumno_id,
                fecha,
                timeSpentOnSite,
                res => {
                  // console.log(res);
                },
                err => {
                  console.log(err);
                }
              );
            },
            err => {
              console.log(err);
            }
          );
        }
      }, this.updateTime);
    }
  }
};
</script>

