<template>
  <v-layout class="componente-login" align-center justify-center>
    <div class="componente-color"></div>
    <v-flex xs12 sm7 md5 lg3 px-2>
      <v-tabs fixed-tabs>
        <v-tab>Profesor</v-tab>
        <v-tab>Alumno</v-tab>
        <v-tab-item>
          <v-card elevation="6" class="pt-8 px-3">
            <div style="max-width: 100px; margin: 0 auto">
              <img src="@/assets/icon.png" width="100%" />
            </div>
            <div style="max-width: 150px; margin: 10px auto 30px">
              <img src="@/assets/name.png" width="100%" />
            </div>
            <v-divider></v-divider>
            <v-form ref="form" action="formulario" @submit.prevent="iniciarSesionProfesor">
              <v-card-text class="pb-2">
                <v-alert
                  v-model="alert"
                  type="error"
                  icon="mdi-cloud-alert"
                  text
                  dismissible
                >Usuario o contraseña incorrecta</v-alert>
                <v-text-field v-model="usuario" :rules="usuarioRules" label="Usuario"></v-text-field>
                <v-text-field
                  v-model="contraseña"
                  :rules="contraseñaRules"
                  label="Contraseña"
                  type="password"
                ></v-text-field>
              </v-card-text>
              <v-card-actions>
                <v-btn
                  :loading="loading"
                  :disabled="loading"
                  class="mb-2"
                  block
                  color="primary"
                  type="submit"
                >Iniciar Sesión</v-btn>
              </v-card-actions>
            </v-form>
          </v-card>
        </v-tab-item>
        <v-tab-item>
          <v-card elevation="6" class="pt-8 px-3">
            <div style="max-width: 100px; margin: 0 auto">
              <img src="@/assets/icon3.png" width="100%" />
            </div>
            <div style="max-width: 150px; margin: 10px auto 30px">
              <img src="@/assets/logo4.jpg" width="100%" />
            </div>
            <v-divider></v-divider>
            <v-form ref="form" action="formulario" @submit.prevent="iniciarSesionAlumno">
              <v-card-text class="pb-2">
                <v-alert
                  v-model="alert"
                  type="error"
                  icon="mdi-cloud-alert"
                  text
                  dismissible
                >Usuario o contraseña incorrecta</v-alert>
                <v-text-field v-model="usuario" :rules="usuarioRules" label="Usuario"></v-text-field>
                <v-text-field
                  v-model="contraseña"
                  :rules="contraseñaRules"
                  label="Contraseña"
                  type="password"
                ></v-text-field>
              </v-card-text>
              <v-card-actions>
                <v-btn
                  :loading="loading"
                  :disabled="loading"
                  class="mb-2"
                  block
                  color="success"
                  type="submit"
                >Iniciar Sesión</v-btn>
              </v-card-actions>
            </v-form>
          </v-card>
        </v-tab-item>
      </v-tabs>
    </v-flex>
  </v-layout>
</template>

<script>
export default {
  data() {
    return {
      usuario: "",
      contraseña: "",
      usuarioRules: [v => !!v || "Usuario es requerido"],
      contraseñaRules: [v => !!v || "Contraseña es requerida"],
      loading: false,
      alert: false
    };
  },
  methods: {
    iniciarSesionProfesor() {
      if (this.$refs.form.validate()) {
        this.loading = true;
        this.$store.state.servicio.profesor.iniciarSesionProfesor(
          this.usuario,
          this.contraseña,
          response => {
            let profesor_id = response["profesor_id"];
            if (profesor_id) {
              this.$store.state.servicio.sesion.iniciarSesion(
                profesor_id,
                "profesor"
              );
              this.$router.push({ name: "cursos" });
            } else {
              this.alert = true;
            }
            this.loading = false;
          },
          error => {
            console.log(error);
            this.loading = false;
          }
        );
      }
    },
    iniciarSesionAlumno() {
      if (this.$refs.form.validate()) {
        this.loading = true;
        this.$store.state.servicio.alumno.iniciarSesionAlumno(
          this.usuario,
          this.contraseña,
          response => {
            let alumno_id = response["alumno_id"];
            if (alumno_id) {
              this.$store.state.servicio.sesion.iniciarSesion(
                alumno_id,
                "alumno"
              );
              this.$router.push({ name: "cursos" });
            } else {
              this.alert = true;
            }
            this.loading = false;
          },
          error => {
            console.log(error);
            this.loading = false;
          }
        );
      }
    }
  }
};
</script>

