  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url("~@/assets/background.jpg");
  background-size: cover;
  filter: blur(2px);
} */
/* .componente-color {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(4, 1, 68, 0.9);
} */
.componente-color {
  width: 100%;
  height: 100%;
  position: absolute;
  background-image: url("~@/assets/pattern5.png");
  background-repeat: repeat;
}
.v-tab {
  margin: 0 !important;
}
</style>
